.nav {
    width: 100%;
    background-color: #a8eef4;
    position: fixed;
    height: 10vh;
    top: 0;
    left: 0;
}

.navbar_navigation {
    display: flex;
    align-items: center;
    height: 100%;
    padding: 0 1rem;
}

.nav-logo {
    margin-left: 1rem;
    font-family: 'Recoleta-Black';
}

.nav-logo a {
    color: #4a4a4a;
    text-decoration: none;
    font-size: 2rem;
}

.nav-items a {
    color: #4a4a4a;
    text-decoration: none;
    /* font-family: 'Recoleta-Bold'; */
    font-family: 'Recoleta-Medium';
    font-size: 30px;
}

.spacer {
    flex: 1 1;
}

.nav-items ul {
    list-style: none;
    margin: 0;
    padding: 0;
    display: flex;
}

.nav-items li {
    padding: 0 0.5rem;
}

.nav-items a:hover,
.nav-items a:active {
    color: red;
    text-decoration: none;
}

@media (max-width: 768px) {
    .nav-items {
        display: none;
    }
}

@media (min-width: 769px) {
    .nav-toggle-btn {
        display: none;
    }

    .nav-logo {
        margin-left: 0;
    }
}
.toggle-button {
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    height: 30px;
    width: 30px;
    background: transparent;
    border: none;
    cursor: pointer;
    padding: 0;
    box-sizing: border-box;
}

.toggle-button:focus {
    outline: none;
}

.toggle-button-line {
    width: 30px;
    height: 2px;
    background: #4a4a4a;
}
.side-drawer {
    height: 100%;
    background: white;
    box-shadow: 1px 0px 3px rgba(0,0,0,0.5);
    position: fixed;
    top: 0;
    left: 0;
    width: 70%;
    max-width: 400px;
    z-index: 200;
    -webkit-transform: translateX(-110%);
            transform: translateX(-110%);
    -webkit-transition: -webkit-transform 0.3s ease-out;
    transition: -webkit-transform 0.3s ease-out;
    transition: transform 0.3s ease-out;
    transition: transform 0.3s ease-out, -webkit-transform 0.3s ease-out;
}

.side-drawer.open {
    -webkit-transform: translateX(0);
            transform: translateX(0)
}

.side-drawer ul {
    height: 100%;
    list-style: none;
    display: flex;
    flex-direction: column;
    justify-content: center;
}

.side-drawer li {
    margin: 0.5rem 0;
}

.side-drawer a {
    color: #4a4a4a;
    text-decoration: none;
    font-size: 2rem;
}

.side-drawer a:hover,
.side-drawer a:active {
    color: red;
}

@media (min-width: 769px) {
    .side-drawer {
        display: none;
    }
}

.backdrop {
    position: fixed;
